@import url('https://fonts.googleapis.com/css2?family=Aboreto&family=Cinzel+Decorative&family=Dosis:wght@300&family=Orbitron&family=Quicksand&family=Russo+One&display=swap');

:root {
    --bg-color: #f2eeff;
    --purple: #9c7be8;
    --purple-dark: #533D98;
    --purple-darker: #322659;
    --purple-light: #E8D6F9;
    --default-font: 'Quicksand', sans-serif;
    --nav-font: 'Dosis', sans-serif;
    --header-font: 'Orbitron', sans-serif;
    --h2-font: 'Cinzel Decorative', serif;
}

*, *::before, *::after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

html {
    cursor: default;
    background-color: var(--bg-color);
    font-size: large;
}

main {
    padding: 30px;
    background-color: var(--bg-color);
    font-family: var(--default-font);
    overflow: auto;
}

h2 {
    font-size: 3em;
    font-family: var(--h2-font);
    color: var(--purple-darker);
    text-align: center;
    margin-bottom: 50px;
    border-bottom: 1px solid var(--purple-dark);
}

.btn {
    border-radius: 10px;
    padding: 15px;
    margin: 10px;
    background-color: var(--purple-dark);
    color: var(--purple-light);
    border: 1px solid var(--purple-dark);
    cursor: pointer;
    transition: 0.5s;

    &:hover {
        background-color: var(--purple);
        color: var(--purple-darker);
    }
}

.btn-link {
    color: var(--purple-dark);
    transition: 0.5s;

    &:hover {
        color: var(--purple);
    }
}

@media screen and (min-width: 400px) {
    html {
        font-size: x-large;
    }
}

@media screen and (min-width: 768px) {
    main {
        height: 100vh;
    }

    h2 {
        font-size: 4em;
    }
}